import axios from "axios";
import router from "@/router";
import { Message } from "element-ui";
import { Loading } from "element-ui";

//定义一个变量  防止提醒两次当ip限制时候
let isFrequencyWarningShown = false;

//创建一个axios实例
const server = axios.create({
  history:true,
  baseURL: "https://8.130.43.227:8085",
  withCredentials: true,
  timeout:50000
});

//定义一个loading变量
let loading;

//请求拦截器
server.interceptors.request.use((config) => {
  //请求发起的时候设置加载状态
  loading = Loading.service({
    text: "正在用命加载，稍等一哈",
    background: "rgba(0, 0, 0, 0.7)",
  });
  //如果长时间未响应则结束加载状态
  setTimeout(() => {
    loading.close()
  }, 5000);
  //获取请求头
  const token = localStorage.getItem("token");
  //若不为null则带上请求头
  if (token != null) {
    config.headers.Authorization = "Bearer "+token;
  }
  return config;
});

//响应拦截器
server.interceptors.response.use((res) => {
  //统一处理后端响应状态为401结果
  if (res.data.code == "401") {
    if (
      router.currentRoute.path != "/" &&
      router.currentRoute.path != "/AssociationWindowSign"
    ) {
      localStorage.removeItem("token");
      localStorage.removeItem("id");
      Message.warning({ message: "认证失败，请重新认证" });
      router.replace("/");
      return res;
    }
  }
  //统一处理后端响应状态为101结果
  if (res.data.code == "101" && !isFrequencyWarningShown) {
    isFrequencyWarningShown = true;
    Message.warning({ message: "请求次数过于频繁，请稍后" });
    setTimeout(() => {
      isFrequencyWarningShown = false;
    }, 2000);
    return res;
  }
  loading.close();
  return res;
});

//暴漏实例
export default server;
