<template>
  <div>
    <div v-show="showCompetition">
      <div class="top">
        <div class="title">
          <span class="left-title">软件</span>
          <span class="right-title">竞赛</span>
          <div class="description">快来参加每月积分赛，提升你的排名吧</div>
        </div>
      </div>
      <div class="center">
        <div class="header">
          <el-card class="left-header-card">
            <div class="title">竞赛入口</div>
            <div class="tip">
              {{ time }}
            </div>
          </el-card>
          <el-card class="right-header-card">
            <div class="prize">奖品入口</div>
            <div class="question" @click="ToQuestionCompetition()">
              积分赛入口
            </div>
            <div class="apply">报名入口</div>
          </el-card>
        </div>
        <div class="bottom">
          <el-card id="zoom" class="left-bottom-card">
            <div style="display: flex">
              <img src="@/assets/competitionIndex/map.png" alt="图片加载失败" />
              <div class="left-bottom-title">
                排名<span style="font-size: 1vw">(积分赛)</span>
              </div>
            </div>

            <div v-if="rankGame.length != 0">
              <div class="bottom-rank" v-for="(item, index) in rankGame" :key="index">
                <span :class="index < 3 ? 'good' : ''">{{ index + 1 }}</span>
                {{item.major}}{{ item.year }}{{ item.username }} <span>{{item.score}}</span>
              </div>
            </div>
            <div v-else>
              <el-empty description="暂无排名"></el-empty>
            </div>
          </el-card>
          <el-card id="zoom" class="right-bottom-card">
            <div class="right-bottom-title">
              <span>往届竞赛回顾</span>
              <span class="cooperation" @click="CompetitionCooperation"
                >竞赛合作</span
              >
            </div>
            <div v-if="lastGame.length != 0">
              <el-descriptions
                :colon="false"
                style="margin-top: 2vh; font-size: 2vh"
              >
                <el-descriptions-item label="以往竞赛"></el-descriptions-item>
                <el-descriptions-item label="时间"></el-descriptions-item>
                <el-descriptions-item label="状态"></el-descriptions-item>
              </el-descriptions>
              <el-descriptions
                :colon="false"
                v-for="(item, index) in 1"
                :key="index"
                style="margin-top: 2vh"
              >
                <el-descriptions-item
                  label="软件竞赛"
                ></el-descriptions-item>
                <el-descriptions-item
                  label="2024-1-17 "
                ></el-descriptions-item>
                <el-descriptions-item label="未开启"></el-descriptions-item>
              </el-descriptions>
              <el-pagination
                :total="total"
                :small="true"
                style="margin: auto; bottom: 1vh; margin-top: 2vh"
                :hide-on-single-page="true"
              ></el-pagination>
            </div>
            <div v-else>
              <el-empty description="暂无比赛数据"></el-empty>
            </div>
          </el-card>
        </div>
      </div>
    </div>
    <div>
      <el-empty
        description="请重置页面宽高"
        v-show="!showCompetition"
      ></el-empty>
    </div>
    <div class="footer">版权信息 © 软件协会竞赛</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: "暂未开放入口。",
      lastGame: [1],
      rankGame: [],
      total: 1,
      showCompetition: true,
    };
  },
  methods: {
    //获取积分赛排名渲染数据
    GetCompetitionRanking() {
      this.$axios.get(`/question/GetCompetitionRanking`).then((res) => {
        this.rankGame = res.data.data
      });
    },
    //进入积分赛入口
    ToQuestionCompetition() {
      this.$router.push({
        path: "/QuestionCompetition",
      });
    },
    //竞赛合作
    CompetitionCooperation() {
      let id = localStorage.getItem("id");
      this.$axios.get(`/question/IfQuestionAdmin?userId=${id}`).then((res) => {
        if (res.data.code == "1") {
          this.$router.push("/QuestionBank/question/管理");
        } else {
          this.$prompt("请输入密钥", "软件协会")
            .then((value) => {
              this.$axios
                .get(
                  `/question/AddQuestionAdmin?key=${value.value}&userId=${id}`
                )
                .then((res) => {
                  if (res.data.code == "1") {
                    this.$router.push("/QuestionBank/question/管理");
                  } else {
                    this.$message.error({ message: "密钥错误" });
                  }
                });
            })
            .catch(() => {});
        }
      });
    },
    //监听用户屏幕是否小于1280像素
    ListenUserNavigator() {
      window.addEventListener("resize", () => {
        let winWidth =
          window.innerWidth ||
          document.body.clientWidth ||
          document.documentElement.clientWidth;
        if (winWidth < 1280) {
          this.showCompetition = false;
        } else {
          this.showCompetition = true;
        }
      });
    },
  },
  mounted() {
    this.GetCompetitionRanking()
    this.ListenUserNavigator();
  },
};
</script>

<style lang="less" scoped>
.top {
  background-color: #2b2b2b;
  height: 40vh;
  .title {
    text-align: center;
    font-size: 5vh;
    .left-title {
      font-weight: bolder;
      -webkit-background-clip: text; /*背景被裁剪到文字*/
      -webkit-text-stroke: 1px white; /*文字描边*/
      -webkit-text-fill-color: transparent; /*设置文字的填充颜色*/
      line-height: 30vh;
    }
    .right-title {
      color: white;
      margin-left: 1vh;
      opacity: 0.6;
      font-size: 4vh;
      font-family: SimHei; /*定义字体*/
      font-weight: bolder; /*定义字体粗细*/
      fill: transparent; /*定义文字透明*/
      stroke: white; /*定义描边颜色*/
      stroke-dasharray: 3;
    }
    .description {
      font-size: 2vh;
      color: white;
      opacity: 0.4;
    }
  }
}
.center {
  .header {
    display: flex;
    justify-content: center;
    margin-top: -3vh;
    .left-header-card {
      width: 25%;
      height: 20vh;
      border-radius: 1vh;
      .title {
        color: black;
        font-weight: bolder;
        font-size: 3vh;
        -webkit-animation: flicker 2s infinite;
      }
      @keyframes flicker {
        0% {
          opacity: 1;
        }
        50% {
          opacity: 0.5;
        }
        100% {
          opacity: 1;
        }
      }
      .tip {
        margin-top: 2vh;
        font-size: 2vh;
        text-indent: 2em;
      }

      &:hover {
        box-shadow: -4px -4px 10px black;
      }
    }
    .right-header-card {
      width: 20%;
      height: 20vh;
      border-radius: 1vh;
      margin-left: 3vh;
      .prize {
        text-align: center;
        margin: 1vh;
        font-family: "华文彩云"; /*设置字体*/
        font-size: 3vh; /*设置字体大小*/
        font-weight: bolder; /*设置字体粗细*/
        -webkit-text-stroke: 1px black; /*文字描边*/
        -webkit-text-fill-color: transparent; /*设置文字的填充颜色*/
        cursor: pointer;
      }
      .question {
        margin: 1vh;
        text-align: center;
        font-size: 3vh; /*设置字体大小*/
        color: #3366ff; /*设置文字颜色*/
        text-shadow: 0 8px 10px #6699ff; /*设置文字阴影*/
        font-weight: bolder; /*设置文字宽度*/
        text-align: center; /*设置文字居中*/
        cursor: pointer;
      }
      .apply {
        text-align: center;
        margin: 1vh;
        font-family: "华文彩云"; /*设置字体*/
        font-size: 3vh; /*设置字体大小*/
        font-weight: bolder; /*设置字体粗细*/
        background: -webkit-linear-gradient(
          top,
          #ff0000,
          #00ff00
        ); /*设置线性渐变*/
        /*为了支持更多的浏览器*/
        -webkit-background-clip: text; /*背景被裁剪到文字*/
        -webkit-text-fill-color: transparent; /*设置文字的填充颜色*/
        cursor: pointer;
      }
      &:hover {
        box-shadow: 4px 4px 10px black;
      }
    }
  }
  .bottom {
    display: flex;
    justify-content: center;
    margin-top: 2vh;
    img {
      width: 4vh;
      height: 3vh;
    }
    .left-bottom-card {
      width: 40%;
      border-radius: 1vh;
      border: none;
      .left-bottom-title {
        font-size: 3vh;
        color: #bdc3c7;
        margin-bottom: 1vh;
      }
      &:hover {
        box-shadow: -4px -4px 10px #c5c5c5;
      }
    }
    .right-bottom-card {
      width: 40%;
      border-radius: 1vh;
      margin-left: 3vh;
      border: none;
      position: relative;
      .right-bottom-title {
        font-size: 3vh;
        display: flex;
        justify-content: space-between;
        color: #595959;
        .cooperation {
          font-size: 1.5vh;
          color: blue;
          margin-top: 1.5vh;
          cursor: pointer;
        }
      }
      &:hover {
        box-shadow: 4px 4px 10px #c5c5c5;
      }
    }
    .bottom-rank {
      padding: 1.5vh;
      padding-top: 2vh;
      border-bottom: 1px solid rgba(0, 0, 0, 0.121);
      border-radius: 1vh;
      justify-content: space-around;
      display: flex;
      .good {
        color: red;
      }
    }
  }
  margin-bottom: 10vh;
}
.footer {
  position: fixed;
  color: #bdc3c7;
  font-size: 1vh;
  right: 2vh;
  bottom: 2vh;
}
</style>