<template>
  <div>
    <div class="top">
      <div>
        <i class="el-icon-search"></i>
        <span> 管理员列表</span>
      </div>
    </div>
    <div class="center" style="margin-top: 2%">
      <el-table
        :data="user"
      >
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column width="250" label="密码" prop="password"></el-table-column>
        <el-table-column label="学院" prop="college"></el-table-column>
        <el-table-column label="专业" prop="major"></el-table-column>
        <el-table-column label="年级" prop="year"></el-table-column>
        <el-table-column label="操作" prop="">
          <template slot-scope="scope">
              <el-popconfirm
                title="是否删除？"
                @confirm="AdminDeleteAdminUser(user[scope.$index].id)"
              >
                <el-button size="mini" slot="reference">删除</el-button>
              </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: [],
      id: "",
    };
  },
  mounted() {
    this.GetAdminUserAll();
  },
  methods: {
    //删除管理员
    AdminDeleteAdminUser(e) {
      this.$prompt("请输入管理员密码", "消息提示")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(
              `/User/AdminDeleteAdminUser?adminpassword=${value.value}&id=${e}`
            )
            .then(
              (res) => {
                console.log(res);
                if (res.data.code == "1") {
                  this.GetAdminUserAll();
                  this.$message.success({ message: "删除成功" });
                } else {
                  this.$message.error({ message: "删除失败" });
                }
              },
              (err) => {
                console.log(err);
                this.$message.error({ message: "系统维护中..." });
              }
            );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //获取全部管理员
    GetAdminUserAll() {
      this.$axios.get("/User/GetAdminUserAll").then(
        (res) => {
          if (res.data.code == "1") {
            this.user = res.data.data;
          }
        },
        (err) => {
          console.log(err);
          this.$message.error({ message: "系统维护中..." });
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
</style>