import router from "vue-router";
import AssociationWindowLogin from "../pages/AssociationWindowLogin";
import AssociationWindowSign from "../pages/AssociationWindowSign";
import AssociationIndex from "../pages/AssociationIndex";
import AssociationAdmin from "../pages/AssociationAdmin";
import IfWindowMobile from "../pages/IfWindowMobile";
import AdminRightUser from "../components/admin/AdminRightUser";
import AdminRightAdminUser from "../components/admin/AdminRightAdminUser";
import AdminRightLeaveWords from "../components/admin/AdminRightLeaveWords";
import AdminBlog from "../components/admin/AdminBlog";
import AdminShareUrl from "../components/admin/AdminShareUrl";
import IndexBottomLeaveWords from "../components/leaveWord/IndexBottomLeaveWords";
import MyUserInfo from "../pages/MyUserInfo";
import ShareUrl from "../components/shareUrl/ShareUrl";
import MyInfo from "../components/myInfo/MyInfo";
import MyLeaveWords from "../components/leaveWord/MyLeaveWords";
import AssociationOpinion from "../components/options/AssociationOpinion";
import MyShareUrl from "../components/myInfo/MyShareUrl";
import AssociationiRecurit from "../components/recrit/AssociationiRecurit";
import IndexBlog from "../components/blog/IndexBlog";
import BlogNew from "../pages/BlogNew";
import BlogDetail from "../pages/BlogDetail";
import MyBlog from "../components/myInfo/MyBlog";
import AddLeaveWord from "@/components/leaveWord/AddLeaveWord.vue";
import AddBlogNew from "@/components/blog/AddBlogNew.vue";
import AddShareUrl from "@/components/shareUrl/AddShareUrl.vue";
import AdminRightUserEdit from "@/components/admin/AdminRightUserEdit.vue";
import WarningTip from "@/components/common/WarningTip.vue";
import MyCollector from "@/components/myInfo/MyCollector.vue";
import ChatBugHelp from "@/components/bugChat/ChatBugHelp.vue";
import UserChat from "@/components/bugChat/UserChat.vue";
import AddBugHelp from "@/components/bugChat/AddBugHelp.vue";
import BugHelpDetail from "../pages/BugHelpDetail.vue";
import AddSaveMessage from "../components/webSocket/AddSaveMessage.vue";
import UserEmail from "../components/common/UserEmail.vue";
import CompetitionIndex from "../components/competition/CompetitionIndex.vue";
import QuestionBank from "../pages/QuestionBank.vue";
import CompetitionRight from "../components/competition/CompetitionRight.vue";
import QuestionCompetition from "../pages/QuestionCompetition.vue";


export default new router({
  // mode:"history",
  routes: [
    {
      path: "/",
      component: AssociationWindowLogin,
    },
    {
      path: "/AssociationWindowSign",
      component: AssociationWindowSign,
      children: [
        {
          path: "UserEmail",
          component: UserEmail,
        },
      ],
    },
    {
      path: "/AssociationIndex",
      component: AssociationIndex,
      children: [
        {
          path: "/",
          redirect: "IndexBlog",
        },
        {
          path: "IndexBlog",
          component: IndexBlog,
          children: [{ path: "WarningTip", component: WarningTip }],
        },
        {
          path: "/CompetitionIndex",
          component: CompetitionIndex,
        },
        {
          path: "/IndexBottomLeaveWords",
          component: IndexBottomLeaveWords,
          children: [{ path: "AddLeaveWord", component: AddLeaveWord }],
        },
        {
          path: "/ShareUrl",
          component: ShareUrl,
          children: [{ path: "AddShareUrl", component: AddShareUrl }],
        },
        {
          path: "/AssociationOpinion",
          component: AssociationOpinion,
        },
        {
          path: "/AssociationiRecurit",
          component: AssociationiRecurit,
        },
        {
          path: "/ChatBugHelp",
          component: ChatBugHelp,
          children: [
            { path: "UserChat", component: UserChat },
            { path: "AddBugHelp", component: AddBugHelp },
            {
              path: "AddSaveMessage",
              component: AddSaveMessage,
            },
          ],
        },
      ],
    },
    {
      path: "/AssociationAdmin",
      component: AssociationAdmin,
      children: [
        {
          path: "/",
          component: AdminRightUser,
          children: [
            { path: "AdminRightUserEdit", component: AdminRightUserEdit },
          ],
        },
        {
          path: "/AdminRightAdminUser",
          component: AdminRightAdminUser,
        },
        {
          path: "/AdminRightLeaveWords",
          component: AdminRightLeaveWords,
        },
        {
          path: "/AdminBlog",
          component: AdminBlog,
        },
        {
          path: "/AdminShareUrl",
          component: AdminShareUrl,
        },
      ],
    },
    {
      path: "/BlogNew",
      component: BlogNew,
      children: [{ path: "AddBlogNew", component: AddBlogNew }],
    },
    {
      path: "/IfWindowMobile",
      component: IfWindowMobile,
    },
    {
      path: "/BlogDetail",
      component: BlogDetail,
    },
    {
      path: "/BugHelpDetail",
      component: BugHelpDetail,
    },
    {
      path: "/MyUserInfo",
      component: MyUserInfo,
      children: [
        {
          path: "/",
          component: MyInfo,
        },
        {
          path: "MyLeaveWords",
          component: MyLeaveWords,
        },
        {
          path: "MyShareUrl",
          component: MyShareUrl,
        },
        {
          path: "MyBlog",
          component: MyBlog,
        },
        {
          path: "MyCollector",
          component: MyCollector,
        },
      ],
    },
    {
      path: "/QuestionBank",
      component: QuestionBank,
      children: [
        {
          path: "question/:subjectType",
          component: CompetitionRight,
        },
      ],
    },
    {
      path: "/QuestionCompetition",
      component: QuestionCompetition,
    },
  ],
});
