<template>
  <div>
    <div class="header">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> 搜索列表</span>
        </div>
        <div>
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-search"
            @click="AdminSearchUser()"
            >搜索</el-button
          >
          <el-button
            size="mini"
            type=""
            icon="el-icon-refresh"
            @click="Refresh()"
            >重置</el-button
          >
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="AdminDeleteUserAll"
            >批量删除</el-button
          >
          <el-button
            size="mini"
            type="info"
            icon="el-icon-s-home"
            @click="ToAssociationIndex()"
            >首页</el-button
          >
        </div>
      </div>
      <div class="bottom" style="margin-top: 2%">
        <el-form style="margin-left: 2%" :inline="true">
          <el-form-item label="社员姓名">
            <div @keyup.enter="AdminSearchUser()">
              <el-input size="small" v-model="searchUser"></el-input>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="center" style="margin-top: 2%">
      <el-table :data="user" @selection-change="onSelectionChange" :header-cell-style="{ 'text-align': 'center' }">
        <el-table-column type="selection"></el-table-column>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="学院" prop="college"></el-table-column>
        <el-table-column label="专业" prop="major"></el-table-column>
        <el-table-column label="年级" prop="year"></el-table-column>
        <el-table-column label="状态" prop="status"></el-table-column>
        <el-table-column width="250" label="操作" prop="">
          <template slot-scope="scope">
            <div style="display: flex; justify-content: space-around">
              <el-button size="mini" @click="Edit(user[scope.$index].id)"
                >编辑</el-button
              >
              <el-popconfirm
                title="是否删除？"
                @confirm="AdminDeleteUser(user[scope.$index].id)"
              >
                <el-button size="mini" slot="reference">删除</el-button>
              </el-popconfirm>
              <el-button
                size="mini"
                @click="AddAdminUser(user[scope.$index].id)"
                >添加</el-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer">
      <el-pagination :total="total" background="" @current-change="onCurrentChange"></el-pagination>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchUser: "",
      user: [],
      pageNum: 1,
      DeleteList: [],
      id: "",
      total:0
    };
  },
  mounted() {
    this.GetUserAll();
    this.$bus.$on("AdminOff", () => {
      this.GetUserAll();
    });
  },
  methods: {
    //添加删除数组
    onSelectionChange(e) {
      this.DeleteList = e
     },
    //设置翻页
    onCurrentChange(e) {
      this.pageNum = e
      this.GetUserAll()
     },
    //重置按钮
    Refresh() {
      this.searchUser = "";
      this.GetUserAll();
    },
    //回到主页
    ToAssociationIndex() {
      this.$router.replace("/AssociationIndex/IndexBlog");
    },
    //处理添加管理员请求
    AddAdminUser(e) {
      this.$prompt("请输入超级管理员密码", "消息提示")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(`/User/AddAdminUser?adminpassword=${value.value}&id=${e}`)
            .then(
              (res) => {
                if (res.data.code == "1") {
                  this.$message.success({ message: "添加成功" });
                } else {
                  this.$message.error({ message: "添加失败" });
                }
              },
              (err) => {
                console.log(err);
                this.$message.error({ message: "系统维护中..." });
              }
            );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //展示编辑
    Edit(e) {
      this.$router.push({
        path: "/AssociationAdmin/AdminRightUserEdit",
        query: {
          id: e,
        },
      });
    },
    AdminDeleteUserAll() {
      if (this.DeleteList.length == 0) return;
      let arr = []
      this.DeleteList.forEach(item => {
        arr.push(item.id)
      })
      this.$confirm("此操作不可逆！", "消息提示")
        .then(() => {
          this.$axios.post("/User/AdminDeleteUserAll", arr).then(
            (res) => {
              if (res.data.code == "1") {
                this.$message.success({ message: "删除成功" });
                this.GetUserAll();
                this.DeleteList = [];
              } else {
                this.$message.error({ message: "删除失败" });
                this.DeleteList = [];
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //处理管理员删除用户
    AdminDeleteUser(e) {
      this.$confirm("此操作不可逆！", "消息提示")
        .then(() => {
          this.$axios.get("/User/AdminDeleteUser?id=" + e).then(
            (res) => {
              if (res.data.code == "1") {
                this.GetUserAll();
                this.$message.success({ message: "删除成功" });
              } else {
                this.$notify.error({
                  message: "管理员不得删除",
                  title: "消息警告",
                  position: "top-left",
                });
              }
            },
            (err) => {
              console.log(err);
              this.$message.error({ message: "系统维护中..." });
            }
          );
        })
        .catch(() => {
          this.$message.info({ message: "操作取消" });
        });
    },
    //搜索用户
    AdminSearchUser() {
      if (this.searchUser == "" || this.searchUser == "\\") return;
      this.GetUserAll()
    },
    //获取用户条数和首页用户10条信息
    GetUserAll() {
      this.$axios.get(`/User/AllUser?pageNum=${this.pageNum}&searchUser=${this.searchUser}`).then(
        (res) => {
          if (res.data.code == "1") {
            this.user = res.data.data.records
            this.total = res.data.data.total
            for (let i = 0; i < this.user.length; i++) {
              if (this.user[i].status == "0") {
                this.user[i].status = "未在线";
              } else {
                this.user[i].status = "在线";
              }
            }
          }
        },
        (err) => {
          this.$message.error({ message: "系统维护中..." });
          console.log(err);
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
.header {
}
.center {
}
.footer {
  margin-top: 2%;
  display: flex;
  justify-content: flex-end;
}
</style>