<template>
  <div>
    <div class="all">
      <el-container>
        <el-aside>
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            background-color="#273446"
            text-color="#fff"
            router
          >
            <el-menu-item index="/AssociationAdmin">
              <i class="el-icon-user"></i>
              <span slot="title">成员管理</span>
            </el-menu-item>
            <el-menu-item index="/AdminRightAdminUser">
              <i class="el-icon-setting"></i>
              <span slot="title">权限管理</span></el-menu-item
            >
            <el-menu-item index="/AdminRightLeaveWords">
              <i class="el-icon-message"></i>
              <span slot="title">历届留言</span></el-menu-item
            >
            <el-menu-item index="/AdminBlog">
              <i class="el-icon-help"></i>
              <span slot="title">个人博客</span></el-menu-item
            >
            <el-menu-item index="/AdminShareUrl">
              <i class="el-icon-s-promotion"></i>
              <span slot="title">网址收录</span></el-menu-item
            >
            <el-menu-item disabled>
              <i class="el-icon-upload"></i>
              <span slot="title">源码下载</span></el-menu-item
            >
            <el-menu-item disabled>
              <i class="el-icon-key"></i>
              <span slot="title">超级管理员</span></el-menu-item
            >
          </el-menu>
        </el-aside>
        <el-main class="right">
          <router-view></router-view>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      activeIndex: this.$route.path,
    };
  },
  mounted() {
    this.id = localStorage.getItem("id");
    this.ToAdmin();
  },
  methods: {
    //判断用户是否是管理员
    ToAdmin() {
      this.$axios.get("/User/IfAdmin?id=" + this.id).then((res) => {
        if (res.data.code == "0") {
          this.$router.replace("/AssociationIndex");
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>

.el-menu {
  text-align: center;
  height: 100vh;
}
.right {
  height: 100vh;
  width: auto;
  overflow: auto;
}
</style>